




















body {
  margin: 0 ;
}
button {
  background-color: rgba(0,0,0);
  color: white;
  border: 1px solid black;
  border-radius: 15%;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity 0.7s;
  transition: opacity 0.7s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
.mainContainer {
  display: grid;
  grid-gap: 10px;
  height: 100vh;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 10px;
}
.backwardsButton {
  position: absolute;
  margin: 20px;
}
.backwardsButton:hover {
  cursor: pointer;
}
.helpButton {
  position: absolute;
  margin: 20px;
  right: 10px;
}
.helpPage {
  visibility: hidden;
  width: 300px;
  height: auto;
  border: 2px solid gold;
  position: absolute;
  right: 20px;
  top: 60px;
  padding: 5px;
  font-size: 13px;
  z-index: 3;
  background-color: white;
}
.helpPageOpen {
  visibility: visible;
}
.helpButton:hover {
  cursor: pointer;
}
.fa-arrow-circle-down:hover {
  background-color: #e1c00d;
  border-radius: 50%;
  cursor: pointer;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.invisibleModal{
  display: none;
}
.editModal {
  display: grid;
  background-color: gold;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  padding: 20px;
  padding-top: 30px;
}
.loadModal {
  display: grid;
  width: 40px;
  height: 40px;
  background: -webkit-gradient(linear, left top, left bottom, from(black), to(white));
  background: linear-gradient(black, white);
  border-radius: 50%;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  justify-items: center;
  -webkit-animation: rotate 0.3s infinite linear;
          animation: rotate 0.3s infinite linear
}
@-webkit-keyframes rotate {
from{-webkit-transform: rotate(0deg);transform: rotate(0deg)
}
to{-webkit-transform: rotate(360deg);transform: rotate(360deg)
}
}
@keyframes rotate {
from{-webkit-transform: rotate(0deg);transform: rotate(0deg)
}
to{-webkit-transform: rotate(360deg);transform: rotate(360deg)
}
}
.innerCircle {
  width: 30px;
  height: 30px;
  background-color: #999999;
  border-radius: 50%;
  justify-items: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.lengthModal {
  font-size: 0.6em;
  display: grid;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  height: 400px;
  width: 250px;
  background-color: gold;
  padding: 10px;
}
.closeButton{
  position: absolute;
  left: 0;
  top: 0;
  margin: 5px;
}
.ideaCloud {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
}
.ideaCloud h2 {
  padding: 5px;
  margin: 20px;
  font-size: 1vw;
  max-height: 250px;
  overflow: auto;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.titleContainer {
  border: 2px solid black;
  display: grid;
  min-height: 150px;
  width: 400px;
  position: relative;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  justify-items: center;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.titleContainer h2 {
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.titleContainer:hover {
  cursor: pointer;
  border: 2px solid #e1c00d;
}
.simpleButton {
  position: absolute;
  right: 0;
  top: 0;
  margin: 30px;
  cursor: pointer;
  background: white;
  border: none;
  color: black;
  font-size: 30px
}
.simpleButton:hover {
  color: #e1c00d
}
.deleteButton {
  position: absolute;
  top: 0;
  right: 0;
  margin: 20px;
}
.infoTab {
  display: none;
  border: none;
  padding: 20px;
}
.infoTabOpen {
  display: block;
  position: absolute;
  background: gold;
  min-height: 200px;
  width: 90%;
  top: 10em;
  overflow-wrap: break-word;
  left: 0;
  z-index: 1;
  border-bottom:10px solid white;
}
.infoTabOpen h2 {
  border: none !important;
  font-size: 0.75vw;
}
.editButton {
  left: 0;
  top: 0;
}
.saveButton {
  display: grid;
  background-color: white;
  border: none;
  top: auto;
  bottom: 0;
  font-size: 10px;
}
.saveButtonImage {
  width: 20px;
}
.showButton {
  display:none;
}
.modal {
  display: none;
}
.modalVisible {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.4);
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.modalVisible:hover .modalSlide {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.modalSlide {
  -webkit-transform: translateY(400%);
          transform: translateY(400%);
}
.modalAddContent {
  display: grid;
  background-color: gold;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.closeModal {
  display: grid;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  justify-items: center;
  width: 300px;
  height: 400px;
  background-color: gold;
  text-align: center;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.closeModal h1 {
  font-size: 20px;
}
.closeModal h2 {
  font-size: 15px;
}
.closeModal button {
  width: auto;
}
.ideaDetail {
  word-break: break-word;
  resize: none;
  width: 100%;
}
.ideaTitle {
  width: 100%;
  word-break: break-word;
  resize: none;
}
.basicForm {
  height: 400px;
  width: 300px;
  margin: 30px;
  background-color: gold;
}
.centreIdea {
  grid-column-start: 2;
  grid-row-start: 2;
  display: grid;
  position: relative;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  border: 2px solid #e1c00d;
}
.centreIdea h1 {
  max-height: 150px;
  overflow: auto;
  margin: 0 10px 0 10px
}
@media screen and (max-width: 1150px){
.mainContainer {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(9, 300px);
    grid-auto-flow: column;
}
.ideaCloud {
    border-top: 1px solid black;
    margin: 20px;
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
}
.ideaCloud h2 {
    font-size: 2.5vw;
}
.centreIdea {
    grid-column-start: 1;
    grid-row-start: 1;
    border: none;
    color: gold;
}
.simpleButton {
    margin: 50px;
}
}
.ideaDot {
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: black
}





















































.modalSave {
  display: grid;
  background-color: gold;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.saveForm {
  padding: 20px
}

.loadContainer {
  width: 400px;
  height: auto;
  padding: 20px;
  display: grid;
  justify-items: center;
  background-color: gold;
}

